<template>
	<view class="tag">
		<!-- 滚动区域 -->
		<view class="tag_contet">
			<view class="tag_area">
				<view class="tag_item" v-for="(item, index) in accepttaglist" :key="index">
					<image :src="item.icon" mode=""></image>
					<view class="">
						{{item.tagname}}
					</view>
				</view>
			</view>
		</view>
		<!-- 进度条 -->
		<!-- <view class="tag_bottom">
			<view class="tag_bottom_inner" :style="innerBarStyle">
				
			</view>
		</view> -->
	</view>
</template>

<script>
	export default {
		props: ["accepttaglist"],
		data() {
			return {// 1.屏幕的宽度
				screenW: 0,
				// 2.滚动内容的宽度
				scrollContentW: 720,
				// 3.滚动条背景的长度
				bgBarW: 100,
				// 4.滚动条的长度
				barXWidth: 0,
				// 5.起点
				startX: 0,
				// 6.记录结束点
				endFlag: 0,
				// 7.移动的距离
				barMoveWidth: 0,
				activeTag: {}
			}
		},
		computed: {
			innerBarStyle() {
			      return {
			        width: `${this.barXWidth}px`,
			        left: `${this.barMoveWidth}px`,
			      };
			    },
		},
		onLoad() {
			// this.getAllTag();\
			
		},
		onShow() {
			console.log("1");
			uni.getSystemInfo({
			      success: function (res) {
			        this.screenW = res.windowWidth
					console.log(this.screenW);
			      }
			    })
			 this.getBottomBarWidth();
			this.bindEvent();
				// this.sendDefaultTag(this.isactive);
		},
		methods: {
			
			// 一开始就传值
			// sendDefaultTag (i) {
			//   this.activeTag = this.tagList[i];
			//   // //同时给被选中的添加激活样式
			//   // this.isactive = i;
			//   this.$emit('func',this.activeTag);
			// },
			// 获取滚动条的长度
			    getBottomBarWidth() {
			      this.barXWidth = this.bgBarW * (this.screenW / this.scrollContentW);
			    },
			    // 移动端事件监听
			    bindEvent() {
			      this.$el.addEventListener("touchstart", this.handleTouchStart, false);
			      this.$el.addEventListener("touchmove", this.handleTouchMove, false);
			      this.$el.addEventListener("touchend", this.handleTouchEnd, false);
			    },
			    // 开始触摸
			    handleTouchStart(event) {
			      // console.log(event.touches);
			      let touch = event.touches[0];
			      this.startX = Number(touch.pageX);
			      // console.log(this.startX);
			    },
			    handleTouchMove(event) {
			      // 1.获取第一个触点
			      let touch = event.touches[0];
			      // 2.求出移动的距离
			      let moveWidth = Number(touch.pageX) - this.startX;
			      // 3.求出滚动条走的距离
			      this.barMoveWidth = -(
			        (this.bgBarW / this.scrollContentW) * moveWidth -
			        this.endFlag
			      );
			      // 4.边界值处理
			      if (this.barMoveWidth <= 0) {
			        this.barMoveWidth = 0;
			      } else if (this.barMoveWidth >= this.bgBarW - this.barXWidth) {
			        this.barMoveWidth = this.bgBarW - this.barXWidth;
			      }
			    },
			    handleTouchEnd() {
			      this.endFlag = this.barMoveWidth;
			    }
		}
	}
</script>

<style>
/* 激活样式 */
.addclass {
  border: 2px solid #ffa800;
  border-radius: 50%;
  background-color: #ffa800;
}
.tag {
  width: 100%;
  height: 180px;
  position: relative;
  background-color: #ffffff;
  padding-bottom: 10px;
}
.tag_content {
  width: 100%;
  overflow-x: scroll;
}
.tag_area {
  width: 630px;
  height: 180px;
  display: flex;
  flex-wrap: wrap;
}
.tag_item {
  width: 70px;
  height: 70px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #666;
}
.tag_item img {
  /* width: 50%; */
  /* padding: 2px; */
  /* margin-bottom: 5px; */
}
.tag_content::-webkit-scrollbar {
  display: none;
}

/* 进度条 */
.tag_bottom {
  width: 100px;
  height: 2px;
  background-color: #ccc;
  position: absolute;
  left: 50%;
  margin-left: -50px;
  bottom: 8px;
}
.tag_bottom_inner {
  position: absolute;
  left: 0;
  height: 100%;
  background-color: #ffa800;
  width: 10px;
}
</style>
